import React, { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { useAuth0 } from "@auth0/auth0-react";
import { Link } from 'react-router-dom'
import { fetchCandidate, candStatus } from '../features/candidates/candidatesSlice'
import { createUser, statusState, loggedUser } from '../features/users/usersSlice'
import { ProfileMenu } from './profileMenu'
import  Home  from '../features/auth/authbutton'


export const Navbar = () => {
  const dispatch = useDispatch()
  const userStatus = useSelector(statusState)
  const candidateStatus = useSelector(candStatus)
  const puser = useSelector(loggedUser)
  const { user, isAuthenticated, isLoading } = useAuth0();
  useEffect(() =>  {
    if (!isAuthenticated) {
      // When puser isn't authenticated, forget any puser info
    } else {
              const uid  = user.sub.slice(6)
              const initialUser = {
                                    uid,
                                    nickname: user.nickname,
                                    verifiedEmail: user.email_verified,
                                    email: user.name
                                  }
            
            
            if( userStatus === 'idle'){
                dispatch(createUser(initialUser))
            }
            /*
            if( candidateStatus === 'idle'){
              dispatch(fetchCandidate(puser._id))
            } */
    }
  });
  const utag = (puser.hasOwnProperty('lastName') && puser.lastName) ? `${puser.firstName} ${puser.lastName}` : puser.nickname
  const ctag = puser.candidate ? 'My Poll' : null
  let Nav;
  if (!isAuthenticated){
    
     Nav = (
             <ul className="nav d-flex justify-content-end">
               <li className="nav-item">
                 <Home/>
               </li>
             </ul>
           )
   }else{
        Nav = (
             <ul className="nav d-flex justify-content-end">
               <li className="nav-item">

                 <Link to="/polls/mypoll" className="text-dark text-dark text-decoration-none">
                  <div className="p-2 mx-2">
                    {ctag}
                  </div>
                 </Link>

               </li>
               <li className="nav-item">
                 <div className="d-flex">
                   <div className="dropdown mr-1" id="dropdowndiv">
                       <a type="button" className="nav-link p-2 mr-5 dropdown-toggle text-capitalize" id="dropdownMenuOffset" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" data-offset="10,20">
                         {utag}
                       </a>
                       <ProfileMenu user={puser}/>
                   </div>
                 </div>
               </li>
             </ul>
           )
   }
  return (
    <nav className="navbar navbar-expand-large navbar-light"  id="navalign">
      <Link to='/vote' className="navbar-brand text-dark" id="brandmove" ><div className="brand">PollitNG</div></Link>
      {Nav}
    </nav>
  )
}
