import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'

export const ProfileComplete = () =>{
  const [candidate, setCandidate] = useState('')
  const dispatch = useDispatch()
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
  })
  return(
      <div
        id="form-body"
        className="form-body">
        <h1 className="form-header text-success">Your Profile is now completed</h1>
        <div className="form-section">
          <p>Tell people in your constituent about your ambition for endorsement</p>
        </div>
        <div className="form-section">
        <Link
          type="button"
          className="btn btn-success mx-2"
          to = '/'
          >
          Continue
          </Link>
        </div>
      </div>
  )
}
