import React, { useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { useWithdrawVoteCandidateMutation } from '../api/apiSlice'
import { loggedUserId, votedCand, myVote } from '../users/usersSlice'


let withdrawVoteButton = ({candidate}) => {
  var [btnAttr, setbtnAttr] = useState('')
  const dispatch = useDispatch()
  const [withdrawVoteCandidate] = useWithdrawVoteCandidateMutation()
  const userId = useSelector(loggedUserId)
  const votedCandidate = userId ? useSelector(state => votedCand(state, candidate.office)):null
  votedCandidate === candidate._id ? btnStatus = '': btnAttr ='hidden'
  
  return(
          <button
          id="votebutton"
          type="button"
          className="btn btn-success"
          onClick={()=>{
                          withdrawVoteCandidate({userId, candidateId: candidate._id, office: candidate.office}),
                          dispatch(myVote({office: candidate.office, candidateId: null}))
                        }
                  }
          hidden={btnAttr}
          >
            Withdraw Vote
          </button>
  )
}

export const WithdrawVoteButton = React.memo(withdrawVoteButton)
