import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Redirect } from 'react-router-dom'
import { FirstName } from './firstname'
import { LastName } from './lastname'
import { Country } from './country'
import { State } from './state'
import { LocalGovt } from './localgovt'
import { Constituent } from './constituent'
import { Nin } from './nin'
import { VoteId } from './voteid'
import { Attestation } from './Attestation'

export const SignUp = () => {
 const regUser = useSelector(state => state.users.currentUser)
 var form;
if(!regUser['firstName']){
  form = <FirstName/>
}else if(!regUser['lastName']){
  form = <LastName/>
}else if(!regUser['country']){
  form = <Country/>
}else if(!(regUser['state'] && regUser['stateId'])){
  form = <State/>
}else if(!(regUser['localGovt'] && regUser['localGovtId'])){
  form = <LocalGovt/>
}else if(!(regUser['constituent'] && regUser['constituentId'])){
  form = <Constituent/>
}else if(!regUser['nin']){
  form = <Nin/>
}else if(!regUser['voteId']){
  form = <VoteId/>
}else {
  form = <Attestation/>
}
  return(
        <div>{form}</div>
  )
}
