import React, { useState, useEffect, useSelector, useMemo } from 'react'
import { useGetMyPollsQuery } from '../api/apiSlice'
import { useSelector } from 'react-redux'
import { loggedUser} from '../users/usersSlice'
import { loggedCand} from '../candidates/candidatesSlice'
import {Spinners} from '../../app/spinner'
import { Link } from 'react-router-dom'


export const MyPollTable = () => {
  const  user = useSelector(loggedUser)
  const {
    data: fpoll = [],
    isLoading,
    isSuccess,
    isError,
    error
  } = useGetMyPollsQuery(user._id)
  const [officeCandidates, setOfficeCandidates] = useState([])
  const [office, setOffice] = useState('')
  const [officeScope, setOfficeScope] = useState('')
 
  const  candidate = useSelector(loggedCand)
  const mypoll = officeCandidates.map(candidate => (
    <article className="table-excerpt"  key={candidate._id}>
      <div><h2 className="text-capitalize" id="candidate-name">{candidate.firstName} {candidate.lastName}</h2></div>
      <div><h5>{candidate.party}</h5></div>
      <div><h3>{candidate.poll}</h3></div>
    </article>
  ))

  useEffect(()=>{
       if(isSuccess){
                            setOfficeCandidates(fpoll.candidates)
                            setOffice(fpoll.office)
                            setOfficeScope(fpoll.officeScope)
                        }
  })
let content 
if(isLoading){
  content = <div><Spinners/></div>
}
else if(isSuccess){
  content = mypoll
}


  return(
          <section className="poll-table">
            <div className="leadtitle-table">
              <Link to={'/vote'}><h1>{officeScope} {office} Poll</h1></Link>
            </div>
            <div className="table-block">{mypoll}</div>
          </section>
        )
}
export const PollTable = React.memo(MyPollTable)
