import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { updateUserCountry, loggedUser } from './usersSlice'


export const Country = () =>{
  const [country, setCountry] = useState('')
  const [backBtnVis, setBackBtnVis] = useState('hidden')
  const [submitBtnVis, setSubmitBtnVis] = useState('hidden')
  const [warningP, setWarningP] = useState('hidden')
  const dispatch = useDispatch()
  const user = useSelector(loggedUser)
  const onSelectNG = e => {
                            setCountry(e.target.value)
                            setSubmitBtnVis('')
                            setWarningP('hidden') 
                            setBackBtnVis('hidden')
                          }
  const onSelectOther = e => {
                                setBackBtnVis('')
                                setWarningP('')
                                setSubmitBtnVis('hidden')
                              }
  const canSubmit = Boolean(country)
  const onSubmitCountry = () =>{
    dispatch(updateUserCountry(country))
  }
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
    if(!user){

    }
  })
  return(
    <div
      id="form-body"
      className="form-body"
      >
      <h1 className="form-header">Are you a Nigerian?</h1>
      <div className="form-section">
      <div className="custom-control custom-radio custom-control-inline mx-2">
        <input
          type="radio"
          id="countryNigerian"
          name="country"
          value="Nigeria"
          className="custom-control-input"
          onChange ={onSelectNG}
          />
        <label className="custom-control-label" htmlFor="countryNigerian">Nigerian</label>
      </div>
      <div className="custom-control custom-radio custom-control-inline mx-2">
        <input
          type="radio"
          id="countryOthers"
          name="country"
          value="Other Countries"
          className="custom-control-input"
          onChange ={onSelectOther}
          />
        <label className="custom-control-label" htmlFor="countryOthers">Other Countries</label>
      </div>
      </div>
      <div 
        className="form-section"
        hidden = {warningP} 
      >
        <p className="text-danger">
          Other countries cannot take part in this poll
        </p>
      </div>
      <div className="form-section">
      <button
        type="button"
        className="btn btn-success mx-2"
        hidden = {submitBtnVis}
        onClick = {onSubmitCountry}
        disabled ={!canSubmit}
        >Submit</button>
      <Link
          to="/"
          type="button"
          className="btn btn-success mx-2"
          hidden = {backBtnVis}
          >
          Back
      </Link>

      </div>
    </div>
  )
}
