import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { useVoteCandidateMutation } from '../api/apiSlice'
import { loggedUserId, myVote} from '../users/usersSlice'


let voteButton = ({candidate}) => { 
  const dispatch = useDispatch();
  const userId = useSelector(loggedUserId)
  const [voteCandidate] = useVoteCandidateMutation()
  
  if (!userId){
    return(null);
  }else{
    return(
            <button
            id="votebutton"
            type="button"
            className="btn btn-success"
            onClick={()=>{ 
                            voteCandidate({userId, candidateId: candidate._id, office: candidate.office}),
                            dispatch(myVote({office: candidate.office, candidateId: candidate._id}))
                          }
                    }
            >
              Vote
            </button>
          )
  }
}

export const VoteButton = React.memo(voteButton)
