import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { updateProfileComplete, updateUser, loggedUser } from './usersSlice'

export const Attestation = () =>{
  const [buttonVis, setButtonVis] = useState('hidden')
  const [proceedBtnVis, setProceedBtnVis] = useState('hidden')
  const [option, setOption] = useState('')
  const [disabled, setDisabled] = useState('')
  const [updateUserStatus, setUpdateUserStatus] = useState('idle')
  const [message, setMessage] = useState('Loading...')
  const user = useSelector(loggedUser)
  const dispatch = useDispatch()
  const onSelectYes = e =>  {setButtonVis(''), setOption('confirmed')}
  const canSubmit = (updateUserStatus === 'idle' && option ==='confirmed')
  const onSubmitProfile = async () => {
                                        if(canSubmit){
                                                      try{
                                                          setUpdateUserStatus('pending')
                                                          await dispatch(updateUser(user)).unwrap()
                                                            setButtonVis('hidden')
                                                            setProceedBtnVis('')
                                                          }catch(err){
                                                            console.error('Failed to save the post: ', err)
                                                          }finally{
                                                            setUpdateUserStatus('idle')
                                                          }
                                                        }
                                        }

  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');

  })
  return(
      <div
      id="form-body"
      className="form-body">
      <h1 className="form-header text-success">Attestation</h1>
      <div className="form-section">
        <div className="form-check form-check-inline">
          <input
            className="form-check-input"
            type="checkbox"
            id="inlineCheckbox1"
            value={option}
            onChange={onSelectYes}
            disabled={disabled}
          />
        </div>
        <div className="mx-2"><p>I attest that the information I have provided are true and reflect my experience</p></div>
      </div>
      <div className="form-section">
      <div>
      <button
        type="button"
        className="btn btn-success mx-2"
        hidden = {buttonVis}
        onClick = {onSubmitProfile}
        disabled = {!canSubmit}
        >
        Submit
        </button>
        </div>
      <div>
        <Link
          to="/candidate/declare"
          type="button"
          className="btn btn-success mx-2"
          hidden = {proceedBtnVis}
          >
          Proceed
          </Link>
        </div>
      </div>
    </div>
  )
}
