// Import the RTK Query methods from the React-specific entry point
import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'
import { loggedUserId, myVote } from '../users/usersSlice'
// Define our single API slice object
export const apiSlice = createApi({
  // The cache reducer expects to be added at `state.api` (already default - this is optional)
  reducerPath: 'api',
  // All of our requests will have URLs starting with '/baseurl'
  baseQuery: fetchBaseQuery({ baseUrl: `${process.env.ServerURL}` }),
  tagTypes: ['Poll'],
  // The "endpoints" represent operations and requests for this server
  endpoints: builder => ({
    // The `getPosts` endpoint is a "query" operation that returns data
    getLandingPolls: builder.query({
        // The URL for the request is '/baseurl/polls'
        query: () =>  '/polls'
      }),
    getPolls: builder.query({
        query: path =>  `/polls${path}`,
        providesTags: ['Poll']
      }),
    getUser: builder.query({
        query: uid =>  `/user/${uid}`
      }),
    getMyPolls: builder.query({
        query: uid =>  `/polls/mypoll/${uid}`
      }),
    createUser: builder.mutation({
        query: userData => ({
          url: '/user/create',
          method: 'POST',
          // Include the entire post object as the body of the request
          body: userData
      })
      }),
    voteCandidate: builder.mutation({
        query: ({userId, candidateId, office}) => ({
          url: `/polls/vote`,
          method: 'POST',
          headers: {
            "Content-type": "application/x-www-form-urlencoded",
          },
          body: new URLSearchParams({userId, candidateId}).toString()
        }),
        async onQueryStarted({userId, candidateId, office}, {dispatch, getState, queryFulfilled}){
          const uid = loggedUserId(getState())
          const patchResult = dispatch(
            apiSlice.util.updateQueryData('getPolls', `?path=${uid}`, draft => {
                const poll = draft.find(candidate => candidate._id === candidateId)
                if(poll){
                  poll.poll+=1
                }
            })
          )
          try{
            await queryFulfilled
          }catch(error){
            patchResult.undo()
            dispatch(myVote({office, candidateId:null}))
          }
        }
    }),
    withdrawVoteCandidate: builder.mutation({
        query: ({userId, candidateId, office}) => ({
          url: `/polls/withdrawvote`,
          method: 'POST',
          headers: {
            "Content-type": "application/x-www-form-urlencoded",
          },
          body: new URLSearchParams({userId, candidateId}).toString()
        }),
        async onQueryStarted({userId, candidateId, office}, {dispatch, getState, queryFulfilled}){
          const uid = loggedUserId(getState())
          const patchResult = dispatch(
            apiSlice.util.updateQueryData('getPolls', `?path=${uid}`, draft => {
                const poll = draft.find(candidate => candidate._id === candidateId)
                if(poll){
                  poll.poll-=1
                }
            })
          )
          try{
            await queryFulfilled
          }catch (error){
                          patchResult.undo()
                          dispatch(myVote({office, candidateId}))
                        }
        }
    })
  })
})

// Export the auto-generated hook for the `getPosts` query endpoint
export const { 
                useGetLandingPollsQuery, 
                useGetUserQuery, 
                useGetPollsQuery, 
                useGetMyPollsQuery, 
                useVoteCandidateMutation,
                useWithdrawVoteCandidateMutation, 
                useCreateUserMutation 
              } = apiSlice
