import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { fetchUser } from '../users/usersSlice'
import {nanoid} from '@reduxjs/toolkit'

const initialState = {
                        isLoggedIn: false,
                        currentUser: {},
                        status: 'idle',
                        error: null
                      }

export const syncOktaAuth = createAsyncThunk('auth/syncOktaAuth', async aUser => {
      const response = new Promise((resolve, reject)  =>  {
    if (aUser.hasOwnProperty('uid')){
          const successObject = aUser
          resolve(successObject)
      }else{
          const errorObject = {
                                  error:  {
                                            message: 'An error occured'
                                          }
                              }
          reject(errorObject)
        }
      })
      .then(data => response.data = data)
      .catch(error => response.data = error)
      return response
})
export const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers:  {
              applogin:{
                reducer(state, action){
                  state.isLoggedIn = true,
                  state.currentUser = action.payload
                }
              },
              applogout:{
                reducer(state, action){
                  state.isLoggedIn = false,
                  state.currentUser = {}
                }
              }
            },
  extraReducers(builder) {
    builder
      .addCase(syncOktaAuth.pending, (state, action) => {
        state.status = 'loading'
      })
      .addCase(syncOktaAuth.fulfilled, (state, action) => {
        state.status = 'succeeded'
        state.isLoggedIn = true
        state.currentUser = action.payload
      })
      .addCase(syncOktaAuth.rejected, (state, action) => {
        state.status = 'failed'
        state.error = action.error.message
      })
  }
})

export const {applogin, applogout } = authSlice.actions
export const selectAuthState = state => state.isLoggedIn;
export const authstatus = state => state.status
export const autherror = state => state.error
export const authuser = state => state.currentUser
export default authSlice.reducer
