import React, { useState, useTransition, useEffect} from 'react'
import { useSelector } from 'react-redux'
import { useDispatch } from 'react-redux'
import { updateUserFirstName } from './usersSlice'

export const FirstName = () =>{
  const [firstName, setFirstName] = useState('')
  const dispatch = useDispatch()
  const onFirstNameChange = e => setFirstName(e.target.value)
  const onSubmitName = () =>{
                              dispatch(updateUserFirstName(firstName));
                            }
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
  })
  return(
    <div
    id="form-body"
    className="form-body"
    >
      <h1 className="form-header">What is your first name?</h1>
      <div className="form-section">
      <input
        className="form-control mx-2"
        onChange={onFirstNameChange}
      />
      <button
        type="button"
        className="btn btn-success mx-2"
        onClick = {onSubmitName}
        >
        Submit
        </button>
       </div>
    </div>
  )
}
