import React from 'react'
import { useSelector } from 'react-redux'
import { PollBanner } from './pollbanner'
import { BlockedPoll } from './blockedPoll'

export const MainPage = () => {
 const regUser = useSelector(state => state.users.currentUser)
 var form;
if(regUser['profilecomplete']){
  form = <PollBanner/>
}else {
  form = <BlockedPoll/>
}
  return(
        <div>{form}</div>
  )
}
