import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import { loggedUser } from '../users/usersSlice'
import { loggedCand } from './candidatesSlice'
import { Office } from './office'
import { Party } from './party'
import { LocalGovt } from './localgovt'
import { State } from './state'
import { Redirect } from 'react-router-dom'
import { ConfirmCandidate } from './confirmcandidate'
import { ProfileComplete } from './profilecomplete'
import { Attestation } from './attestation'

export const CandSignUp = () => {
 const regCand = useSelector(loggedCand)
 const regUser = useSelector(loggedUser)

 var form;
if(regUser['candidate']){
  form = <Redirect to="/"/>
}else if (!regCand['uid']){
  form = <ConfirmCandidate/>
}else if (!(regCand['partyName'] && regCand['partyAbbv'])){
  form = <Party/>
}else if(!(regCand['state'] && regCand['stateId'])){
  form = <State/>
}else if(!(regCand['localGovt'] && regCand['localGovtId'])){
  form = <LocalGovt/>
}else if(!regCand['office']){
  form = <Office/>
}else{
  form = <Attestation/>
}


  return(
        <div>{form}</div>
  )
}
