import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { updateUserLastName } from './usersSlice'

export const LastName = () =>{
  const [lastName, setLastName] = useState('')
  const dispatch = useDispatch()
  const onLastNameChange = e => setLastName(e.target.value)
  const onSubmitName = () =>{
                              dispatch(updateUserLastName(lastName));
                            }
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
  })
  return(
    <div
      id="form-body"
      className="form-body"
      >
      <h1 className="form-header">What is your last name?</h1>
      <div className="form-section">
      <input
        className="form-control mx-2"
        onChange={onLastNameChange}
      />
      <button
        type="button"
        className="btn btn-success mx-2"
        onClick = {onSubmitName}
        >Submit</button>
        </div>
    </div>
  )
}
