import React from 'react'
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Redirect,
  useNavigate
} from 'react-router-dom'
import { Auth0Provider, withAuthenticationRequired } from '@auth0/auth0-react'
import { Navbar } from './app/Navbar'
import { LandingPage } from './features/polls/landingpage'
import { MainPage } from './features/polls/mainpage'
import { PollTable } from './features/polls/polltable'
import { SignUp } from './features/users/signup'
import { CandSignUp } from './features/candidates/CandSignUp'
import { MyPollTable } from './features/polls/mypolltable'
import Profile from './features/users/profile'
const office = {office:'presidential', officeId:'x06PR0112pl'}
const domain = process.env.OKTADOMAIN
const cliendId = process.env.CLIENTID
const ProtectedRoute = ({ component, ...args }) => {
  const Component = withAuthenticationRequired(component, args)
  return <Component />
}
const Auth0ProviderWithRedirectCallback = ({ children, ...props }) => {
  const navigate = useNavigate();
  const onRedirectCallback = (appState) => {
    navigate((appState && appState.returnTo) || window.location.pathname);
  };
  return (
    <Auth0Provider onRedirectCallback={onRedirectCallback} {...props}>
      {children}
    </Auth0Provider>
  );
};
const Main = () => {
  return(
    <React.Fragment>
    <MainPage/>
  </React.Fragment>
  )
}
function App(){
  return (
    <Router>
      <Auth0ProviderWithRedirectCallback
        domain={domain}
        clientId={cliendId}
        redirectUri={window.location.origin}
      >
      <Navbar/>
      <Routes>
      <Route exact path="/" element ={<LandingPage office={office}/>}/>
      <Route
        exact 
        path="/vote"
        element={<ProtectedRoute component={Main}/>}
      />
      <Route
        exact
        path="/polls/:officeId/:office"
        element={<ProtectedRoute component={PollTable}/>}
        />
      <Route exact path="/polls/mypoll" element={<ProtectedRoute component={MyPollTable}/>} />
      <Route exact path="/user/signup" element={<ProtectedRoute component={SignUp}/>}/>
      <Route exact path="/candidate/declare" element={<ProtectedRoute component={CandSignUp}/>}/>
      <Route path="/profile" element={<ProtectedRoute component={Profile}/>}/>
      </Routes>
      </Auth0ProviderWithRedirectCallback>
    </Router>
  ); 
}
 
export default App
