import React, { useState, useEffect, useSelector } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { updateUserConstituent, updateUserConstituentId, loggedUser } from './usersSlice'

export const Constituent = () =>{
  const user = useSelector(loggedUser)
  const [constituent, setConstituent] = useState('')
  const [constituentId, setConstituentId] = useState('hidden')
  const [span, setSpan] = useState('hidden')
  const [conspan, setConspan] = useState('')
  const [cons, setCons] =  useState([])
  const [psdCons, setPsdCons] = useState([])
  const [inputValue, setInputValue] = useState('')
  const [error, setError] = useState({})
  const dispatch = useDispatch()
  const onConChange = e => {
    if(e.target.value.length > 0){
                                setInputValue(e.target.value)
                                setSpan('')
                                const regex = new RegExp(`^${e.target.value}`, 'i')
                                const foundCons = cons.filter(({name}) => name.match(regex))
                                setPsdCons(foundCons)
                                if(cons.some(({name})=> name.toLowerCase() === e.target.value.toLowerCase())){
                                  setConstituentId(foundCons[0]._id)
                                  setConstituent(e.target.value.toLowerCase())
                                  setPsdCons([])
                                  setConspan('hidden')
                                }else{
                                  setConstituent('')
                                }
                              }else{
                                setInputValue('')
                                setPsdCons([])
                                setSpan('hidden')
                              }
}
  const onSelectCon = e => {
                                setInputValue(e.target.innerHTML)
                                setPsdCons([])
                                setSpan('hidden')
                                if(cons.some(({name})=> name === e.target.innerHTML) ){
                                  setConstituentId(e.target.dataset.id)
                                  setConstituent(e.target.innerHTML.toLowerCase())
                                  setConspan('hidden')
                                }
                              }
  const canSubmit = Boolean(constituent)
  const onSubmitCon = () =>{
                            dispatch(updateUserConstituentId(constituentId))
                            dispatch(updateUserConstituent(constituent))
                          }
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
    if(!cons.length>0){
                          fetch(`${process.env.ServerURL}/constituent/sr/${user.localGovtId}`)
                          .then((response)=> response.json())
                          .then(fcons => {
                              setCons(fcons)
                          })
                          .catch(error => {
                                            setError(error)
                                        })
                        }
  })
  const parsedCons = psdCons.map(con =>(
    <span className="parsedobjects-items" key={con._id} data-id={con._id} type="button"
    onClick = {onSelectCon}
    >
      {con.name}
    </span>
  ))
  const listcons = cons.map(con => (
    <li className="list-group-item font-weight-lighter font-italic" key={con._id}>{con.name}</li>
  ))
  return(
    <div
      id="form-body"
      className="form-body">
      <h1 className="form-header">Which state constituency will you be voting from?</h1>
      <div className="form-section">
        <input
          className="form-control mx-2"
          onChange={onConChange}
          value={inputValue}
        />
        <button
          type="button"
          className="btn btn-success mx-2"
          onClick = {onSubmitCon}
          disabled = {!canSubmit}
          >Submit
        </button>
      </div>
      <div className="parsedobjects" hidden={span}>{parsedCons}</div>
      <div className="parsedobjects" hidden={conspan}>
          <p className="font-weight-lighter font-italic">Find below constituents in <span className="text-capitalize">{user.localGovt}</span></p>
          <ul className="list-group list-group-flush">
            {listcons}
          </ul>
      </div>
    </div>
  )
}
