import { createSlice, createAsyncThunk, createSelector, createEntityAdapter } from '@reduxjs/toolkit'
const landPollsAdapter = createEntityAdapter({
                                            selectId: (e) => e._id,
                                            sortComparer: (a,b)=>b.poll - a.poll
                                          })
const initialState = landPollsAdapter.getInitialState({
                                                    status  : 'idle',
                                                    error   : null
                                                  })

export const fetchLandingPolls = createAsyncThunk('landpolls/fetchLandingPolls', async () => {
      const init =  {
                      method: 'GET'
                    }
        const response = await fetch(`${process.env.ServerURL}/polls`, init)
          if (response.ok){
            const jsonValue = await response.json()
            return Promise.resolve(jsonValue)
          }
})

const landPollsSlice = createSlice({
  name: 'landpolls',
  initialState,
  reducers: {
    purgelandpolls:{
      reducer(state){
          state.landpolls = []          
      }
    },
},
  extraReducers(builder) {
    builder
      .addCase(fetchLandingPolls.pending, (state, action) => {
        state.status = 'loading'
      })
      .addCase(fetchLandingPolls.fulfilled, (state, action) => {
        state.status = 'succeeded'
        // Add any fetched posts to the array
        landPollsAdapter.upsertMany(state, action.payload)
      })
      .addCase(fetchLandingPolls.rejected, (state, action) => {
        state.status = 'failed'
        state.error = action.error.message
      })
  }
                                })

export const { purgelandpolls } = landPollsSlice.actions
export const landPollStatus = state => state.landpolls.status
export const landPollErrorStatus = state => state.landpolls.error
export const {selectAll: selectAllLandPolls} = landPollsAdapter.getSelectors(state => state.landpolls)
export const selectLandPoll = createSelector([selectAllLandPolls, (state, uid) => uid], (polls, uid) =>   polls.filter(poll => poll.officeId === uid))

export default landPollsSlice.reducer
