import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import { useSelector, useDispatch } from 'react-redux'

const initialState = {
                        candidate:{
                                      
                                    },
                        status:'idle',
                        error:  null
                      }
export const fetchCandidate = createAsyncThunk('candidates/fetchCandidate', async (uid, thunkAPI) => {
      const init =  {
                      method: 'GET'
                    }
        const response = await fetch(`${process.env.ServerURL}/candidate/${uid}`, init)
          if (response.ok){
            const jsonValue = await response.json()
            return Promise.resolve(jsonValue)
          }
})
export const updateCandidate = createAsyncThunk('candidates/updateCandidate', async (params, thunkAPI) => {
  let cbody = new URLSearchParams(params).toString();
  const init = {
                  method: 'POST',
                  headers:  {'Content-Type':'application/x-www-form-urlencoded'},
                  body:  cbody
                }
  const response = await fetch(`${process.env.ServerURL}/candidate/create`, init)
                            if(response.ok){
                              const jsonValue = await response.json()
                              return Promise.resolve(jsonValue)
                            }
})
const candidatesSlice = createSlice({
                                      name: 'candidates',
                                      initialState,
                                      reducers: {
                                                    declareCandidate:{
                                                      reducer(state, action){
                                                        state.candidate.uid = action.payload
                                                      }
                                                    },
                                                    updateCandidatePartyAbbv:{
                                                      reducer(state, action){
                                                        state.candidate.partyAbbv = action.payload
                                                        }
                                                      },
                                                    updateCandidatePartyName:{
                                                      reducer(state, action){
                                                        state.candidate.partyName = action.payload
                                                        }
                                                      },
                                                    updateCandidateOffice:{
                                                          reducer(state, action){
                                                            state.candidate.office = action.payload
                                                          }
                                                        },
                                                    updateCandidateOfficeId:{
                                                      reducer(state, action){
                                                        state.candidate.officeId = action.payload
                                                      }
                                                    },
                                                    updateCandidateOfficeScope:{
                                                      reducer(state, action){
                                                        state.candidate.officeScope = action.payload
                                                      }
                                                    },
                                                    updateCandidateOfficeScopeId:{
                                                      reducer(state, action){
                                                        state.candidate.officeScopeId = action.payload
                                                      }
                                                    },
                                                    updateCandidateState:{
                                                              reducer(state, action){
                                                                state.candidate.state = action.payload
                                                              }
                                                            },
                                                    updateCandidateStateId:{
                                                                      reducer(state, action){
                                                                        state.candidate.stateId = action.payload
                                                                      }
                                                                    },
                                                    updateCandidateLocalGovt:{
                                                          reducer(state, action){
                                                            state.candidate.localGovt = action.payload
                                                          }
                                                        },
                                                    updateCandidateLocalGovtId:{
                                                              reducer(state, action){
                                                                state.candidate.localGovtId = action.payload
                                                              }
                                                            }
                                                },
                                      extraReducers(builder) {
                                                  builder
                                                  .addCase(updateCandidate.pending, (state, action) => {
                                                    state.status = 'loading'
                                                  })
                                                  .addCase(updateCandidate.fulfilled, (state, action) => {
                                                    state.status = 'succeeded'
                                                    state.candidate = action.payload
                                                    
                                                  })
                                                  .addCase(updateCandidate.rejected, (state, action) => {
                                                    state.status = 'failed'
                                                    state.error = action.error.message
                                                  })
                                                  .addCase(fetchCandidate.pending, (state, action) => {
                                                    state.status = 'loading'
                                                  })
                                                  .addCase(fetchCandidate.fulfilled, (state, action) => {
                                                    state.status = 'succeeded'
                                                    state.candidate = action.payload
                                                  })
                                                  .addCase(fetchCandidate.rejected, (state, action) => {
                                                    state.status = 'failed'
                                                    state.error = action.error.message
                                                  })
                                                }
                                  })

export const {
                declareCandidate,
                updateCandidatePartyName,
                updateCandidatePartyAbbv,
                updateCandidateOffice,
                updateCandidateOfficeId,
                updateCandidateOfficeScope,
                updateCandidateOfficeScopeId,
                updateCandidateState,
                updateCandidateStateId,
                updateCandidateLocalGovt,
                updateCandidateLocalGovtId
              } = candidatesSlice.actions;

export default candidatesSlice.reducer

export const loggedCand = state => state.candidates.candidate
export const candStatus = state => state.candidates.status