import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import  Home  from '../features/auth/authbutton'


export const ProfileMenu = (props) => {

	return (
						<div className="bg-light border-0 shadow dropdown-menu dropdown-menu-right mt-1 p-2 rounded" id="dropMenu" aria-labelledby="dropdownMenuOffset" style={{fontSize:'.500rem'}}>
								<div className="d-flex justify-content-center mt-3"><h2><span className="badge badge-secondary rounded-circle text-uppercase p-3">{(props.user.hasOwnProperty('lastName')&& props.user.lastName)?`${props.user.firstName.charAt(0)}${props.user.lastName.charAt(0)}`:(props.user.hasOwnProperty('nickname')?props.user.nickname.charAt(0): '')}</span></h2></div>
								<div className="dropdown-header justify-content-center text-capitalize p-2">
								<div className="d-flex align-items-center flex-column">
									<div className="flex-row"><h6>{(props.user.hasOwnProperty('lastName') && props.user.lastName) ? `${props.user.firstName} ${props.user.lastName}` : props.user.nickname}</h6></div>
									<div className="flex-row" style={{fontSize:'.600rem'}}><Link to="/profile" className="dropdown-item float-left">Account</Link></div>
								</div>
								</div>
								<div className="dropdown-divider"></div>
								<div className="d-flex flex-row justify-content-center">
									<Home/>
								</div>
						</div>
			)
}
