import { createSlice, createAsyncThunk, createSelector } from '@reduxjs/toolkit'

const initialState = {
                       currentUser: {},
                            status: 'idle',
                             error: null
                          }

export const fetchUser = createAsyncThunk('users/fetchUser', async (uid, {dispatch}) => {
      const init =  {
                      method: 'GET'
                    }
        const response = await fetch(`${process.env.ServerURL}/user/${uid}`, init)
          if (response.ok){
            const jsonValue = await response.json()
            return Promise.resolve(jsonValue)
          }
})
export const createUser = createAsyncThunk('users/createUser', async (params, {getState, dispatch}) => {
  let cbody = new URLSearchParams(params).toString();
  const init = {
                method: 'POST',
                headers:  {'Content-Type':'application/x-www-form-urlencoded'},
                body:  cbody
                }
  const response = await fetch(`${process.env.ServerURL}/user/create`, init)
                            if(response.ok){
                              
                              const jsonValue = await response.json()
                              return Promise.resolve(jsonValue) 
                            }
})
export const updateUser = createAsyncThunk('users/updateUser', async (params, thunkAPI) => {
  let cbody = new URLSearchParams(params).toString();
  const init = {
                  method: 'POST',
                  headers:  {'Content-Type':'application/x-www-form-urlencoded'},
                  body:  cbody
                }
  const response = await fetch(`${process.env.ServerURL}/user/update`, init)
                            if(response.ok){
                              const jsonValue = await response.json()
                              return Promise.resolve(jsonValue)
                            }
})

const usersSlice = createSlice({
  name: 'users',
  initialState,
  reducers: {
                setUser:{
                                          reducer(state, action){
                                          state.currentUser = action.payload
                  }
                },
                updateUserFirstName:{
                                          reducer(state, action){
                                          state.currentUser.firstName = action.payload}
                  },
                updateUserLastName:{
                                          reducer(state, action){
                                          state.currentUser.lastName = action.payload}
                  },
                updateUserCountry:{
                                          reducer(state, action){
                                          state.currentUser.country = action.payload}
                      },
                updateUserState:{
                                          reducer(state, action){
                                          state.currentUser.state = action.payload }
                        },
                updateUserStateId:{
                                          reducer(state, action){
                                          state.currentUser.stateId = action.payload }
                        },
                updateUserLocalGovt:{
                                          reducer(state, action){
                                          state.currentUser.localGovt = action.payload}
                                      },
                updateUserLocalGovtId:{
                                          reducer(state, action){
                                          state.currentUser.localGovtId = action.payload}
                                      },
                updateUserConstituent:{
                                          reducer(state, action){
                                          state.currentUser.constituent = action.payload}
                                      },
                updateUserConstituentId:{
                                          reducer(state, action){
                                          state.currentUser.constituentId = action.payload}
                                      },
                updateUserNin:{
                                          reducer(state, action){
                                          state.currentUser.nin = action.payload
                                                        }
                                    },
                updateUserVoteId:{
                                          reducer(state, action){
                                          state.currentUser.voteId = action.payload
                                                          }
                                    },
                verifyUserNin:{
                                          reducer(state, action){
                                          state.currentUser.verifiedNin = true;
                                  }
                                },
                verifyUserVoteId:{
                                          reducer(state, action){
                                          state.currentUser.verifiedVoteId = true;
                              }
                            },
                myVote: {
                                  reducer(state,action){
                                  state.currentUser.votedOffices[action.payload.office] = action.payload.candidateId
                                  }
                },
                purgeUser:{
                                  reducer(state, action){
                                  state.currentUser = {}
                              }
                }
            },
  extraReducers(builder) {
    builder
    .addCase(fetchUser.pending, (state, action) => {
      state.status = 'loading'
    })
    .addCase(fetchUser.fulfilled, (state, action) => {
      state.status = 'succeeded'
      state.currentUser = action.payload
    })
    .addCase(fetchUser.rejected, (state, action) => {
      state.status = 'failed'
      state.error = action.error.message
    })
    .addCase(createUser.pending, (state, action) => {
      state.status = 'loading'
    })
    .addCase(createUser.fulfilled, (state, action) => {
      state.status = 'succeeded'
      state.currentUser = action.payload
    })
    .addCase(createUser.rejected, (state, action) => {
      state.status = 'failed'
      state.error = action.error.message
    })
    .addCase(updateUser.pending, (state, action) => {
      state.status = 'loading'
    })
    .addCase(updateUser.fulfilled, (state, action) => {
      state.status = 'succeeded'
      state.currentUser = action.payload
    })
    .addCase(updateUser.rejected, (state, action) => {
      state.status = 'failed'
      state.error = action.error.message
    })
  }
          });

export const {
                setUser,
                updateUserFirstName,
                updateUserLastName,
                updateUserCountry,
                updateUserState,
                updateUserStateId,
                updateUserLocalGovt,
                updateUserLocalGovtId,
                updateUserConstituent, 
                updateUserConstituentId,
                updateUserNin,
                updateUserVoteId,
                updateProfileComplete,
                verifyUserNin,
                verifyUserVoteId,
                myVote,
                purgeUser
              } = usersSlice.actions;

export default usersSlice.reducer

export const loggedUser = state => state.users.currentUser
export const loggedUserId = state => state.users.currentUser.uid
export const errorState = state => state.users.error
export const statusState = state => state.users.status

//export const votedCand = (state, office) => state.users.currentUser.votedOffices[office]

export const votedCand = createSelector(
  [loggedUser, (state, office) => office], (user, office) => user.votedOffices[office]
)
 