import { CompleteProfileButton } from '../users/completeprofilebutton'

export const BlockedPoll = () => {

  return (
    <section className="poll-list">
      <div className="leadtitle">
        <h1>PollitNG Polls</h1>
      </div>
      <div className="presidential-block">
        <article className="poll-excerpt">
          <h3>Complete your profile to view your specific poll</h3>
          <h5>This poll is unique to your voting location</h5>
          <h6>Some polls maybe empty this is because they are yet to be filled. Logout and login again to update poll</h6>
          <div className="blockMessageLogin"><CompleteProfileButton/></div>
        </article>
      </div>
    </section>
  )
}
