import React, {useMemo} from 'react'
import { useSelector } from 'react-redux'
import { createSelector } from '@reduxjs/toolkit'
import { votedCand, loggedUser} from '../users/usersSlice'
import { Link, useParams } from 'react-router-dom'
import {VoteButton} from './voteButton'
import {WithdrawVoteButton} from './withdrawVoteButton'
import { useGetPollsQuery } from '../api/apiSlice'


let pollTable = () => {
  let { office } = useParams()
  let {officeId } = useParams()
  const  user = useSelector(loggedUser)
  const selectOfficePoll = useMemo(()=>{
    const emptyPolls = [] 
    return createSelector(
      res => res.data,
      (res, officeId) => officeId,
      (data, officeId) => data?.filter(poll => poll.officeId === officeId) ?? emptyPolls
    )
  }, []) 
  const {orderedCandidates} = useGetPollsQuery(`?path=${user.uid}`, {
      selectFromResult: result => ({
        ...result,
        orderedCandidates: selectOfficePoll(result, officeId)
      })
  })
  
  const votedCandidate = useSelector(state => votedCand(state, office))
  const officeCandidates = orderedCandidates.map(candidate => (
    <article className="table-excerpt"  key={candidate._id}>
      <div><h2 className="text-capitalize" id="candidate-name">{candidate.firstName} {candidate.lastName}</h2></div>
      <div><h5>{candidate.party}</h5></div>
      <div><h3>{candidate.poll}</h3></div>
      <div>
      { user.hasOwnProperty('votedOffices')
        ?
        !votedCandidate ?  <VoteButton candidate={candidate}/>:<WithdrawVoteButton candidate={candidate}/>
        :
        null
      }
      </div>
    </article>
  ))

  return(
          <section className="poll-table">
            <div className="leadtitle-table">
              <Link to={'/vote'}><h1>{office} Poll</h1></Link>
            </div>
            <div className="table-block">{officeCandidates}</div>
          </section>
        )
}
export const PollTable = React.memo(pollTable)
