import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { loggedUser } from '../users/usersSlice'
import { declareCandidate} from './candidatesSlice'


export const ConfirmCandidate = () =>{
  const [candidate, setCandidate] = useState('')
  const [buttonVis, setButtonVis] = useState('hidden')
  const [declareBtnVis, setDeclareBtnVis] = useState('hidden')
  const user = useSelector(loggedUser)
  const dispatch = useDispatch()
  const onSelectYes = e =>  {setButtonVis('hidden'), setDeclareBtnVis('')}
  const onSelectNo = e => { setDeclareBtnVis('hidden'), setButtonVis('') }
  const onDeclareBtn = ()=>{
                              dispatch(declareCandidate(user._id))
                            }
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
  })
  return(
      <div
      id="form-body"
      className="form-body">
      <h1 className="form-header text-success">Your Profile is now completed</h1>
      <div className="form-section">
        <div className="mx-2"><p>Are you a canditate vying for an office?</p></div>
        <div className="custom-control custom-radio custom-control-inline">
          <input
            type="radio"
            id="yes"
            name="candidate"
            value="true"
            className="custom-control-input"
            onChange ={onSelectYes}
            />
          <label className="custom-control-label" htmlFor="yes">Yes</label>
        </div>
        <div className="custom-control custom-radio custom-control-inline">
          <input
            type="radio"
            id="no"
            name="candidate"
            value='false'
            className="custom-control-input"
            onChange ={onSelectNo}
            />
          <label className="custom-control-label" htmlFor="no">No</label>
        </div>
      </div>
      <div className="form-section">
        <div>
        <Link
          type="button"
          className="btn btn-success mx-2"
          to='/'
          hidden={buttonVis}
          >
            Vote Now
          </Link>
        </div>
        <div>
        <button
          type="button"
          className="btn btn-success mx-2"
          onClick={onDeclareBtn}
          hidden={declareBtnVis}
          >
           Declare Now
          </button>
        </div>
      </div>
    </div>
  )
}
