import React, {useMemo} from 'react'
import { Participate } from './participatebutton'
import { useSelector} from 'react-redux'
import { selectLandPoll } from './landingPageSlice' 
import { useGetLandingPollsQuery } from '../api/apiSlice'


export const LandingPage = (props) => {

  const {
    data: polls = [],
    isLoading,
    isSuccess,
    isError,
    error
  } = useGetLandingPollsQuery()
  
  const sortedpolls = useMemo(()=>{
    const sortedpolls = polls.slice()
    sortedpolls.sort((a, b) => b.poll - a.poll)
    return sortedpolls
  }, [polls])

  //const orderedCandidates = useSelector(state => selectLandPoll(state, props.office.officeId)); 
  const orderedCandidates = sortedpolls.filter(poll => poll.officeId === props.office.officeId)
  const topThreeCandidates = orderedCandidates.filter((a, b)=> b < 3)
  const albPartyCandidates = topThreeCandidates.slice().sort((a,b)=>{
                                                  if (a.party < b.party){
                                                    return -1
                                                  }
                                                  if (a.party > b.party){
                                                    return 1
                                                  }
                                                  return 0
                                                                      })

const officeCandidates = albPartyCandidates.map(candidate => (
  <article className="poll-excerpt" key={candidate._id}>
    <h2 className="poll-candidate-name">{candidate.firstName} {candidate.lastName}</h2>
    <h5>{candidate.party}</h5>
    <h3>{candidate.poll}</h3>
  </article>
))

  return (
    <section className="poll-list">
      <div className="leadtitle">
          <h1>{props.office.office} Poll</h1>
      </div>
      <div className="presidential-block">
          {officeCandidates}
      </div>
      <div className="landing-login">
          <Participate/>
      </div>
    </section>
  )
}
