import React from 'react'
import { useSelector} from 'react-redux'
import { loggedUser } from '../users/usersSlice'
import { useGetPollsQuery } from '../api/apiSlice'
import {OfficePoll} from './officePoll'
import {BlockedPoll} from './blockedPoll'

export const PollBanner = () => {
  const user = useSelector(loggedUser)
  const {
      data: polls = [],
      isLoading,
      isSuccess,
      isError,
      error
    } = useGetPollsQuery(`?path=${user.uid}`)
  


const otherOffices = [
                        {office:'presidential',officeId:'x06PR0112pl', key:0},
                        {office:'senate', officeId:'x05SE0306se', key:1},
                        {office:'houseOfRep', officeId:'x04HRxx10hp', key:2},
                        {office:'governorship', officeId:'x03GO0112gp', key:3},
                        {office:'stateRep', officeId:'x02SRxx08sp', key:4},
                        {office:'localGovt', officeId:'x01LGxx09lt', key:5}
                      ]
const PollOffices = otherOffices.map((selectOffice)=>(
    <div className={selectOffice.key===0?'carousel-item active':'carousel-item'}  key={selectOffice.key}> 
      <OfficePoll office={selectOffice}/>
    </div>
  ))
const BlockedOffices = otherOffices.map((selectOffice)=>(
    <div className={selectOffice.item} key={selectOffice.key}>
      <BlockedPoll office={selectOffice}/>
    </div>
  ))
  
let displayedOffices;
  if(user.uid && user.profilecomplete){
      displayedOffices = PollOffices
  }else{
      displayedOffices = BlockedOffices
  }
  return(
    <div className='position-sticky'>
      <div id="carouselExampleIndicators" className="carousel slide" data-ride="carousel" >
          <ol className="carousel-indicators" hidden>
            <li data-target="#carouselExampleIndicators" data-slide-to="0" className="active"></li>
            <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
            <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
            <li data-target="#carouselExampleIndicators" data-slide-to="3"></li>
            <li data-target="#carouselExampleIndicators" data-slide-to="4"></li>
            <li data-target="#carouselExampleIndicators" data-slide-to="5"></li>
          </ol>
          <div className="carousel-inner" id="carousel-inner" >
            {displayedOffices}
          </div>
          <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
            <span className="carousel-control-prev-icon" id="carousel-control-prev-icon" aria-hidden="true"></span>
            <span className="sr-only">Previous</span>
          </a>
          <a className="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
            <span className="carousel-control-next-icon" aria-hidden="true"></span>
            <span className="sr-only">Next</span>
          </a>
      </div>
    </div>
  )
}
