import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { updateUserNin } from './usersSlice'

export const Nin = () =>{
  const [nin, setNin] = useState('')
  const [inputValue, setInputValue] = useState('')
  const [inputVis, setInputVis] = useState('hidden')
  const [btnVis, setBtnVis] = useState('hidden')
  const dispatch = useDispatch()
  const onSelectYes = e =>  { setInputVis(''), setBtnVis(''), setNin('') }
  const onSelectNo = e => {setInputVis('hidden'), setBtnVis(''), setNin('pending')}
  const canSubmit = Boolean(nin)
  const onNinChange = e => {
                              const regex1 = new RegExp(/\D/)
                              if(!e.target.value.toString().match(regex1)){
                                setInputValue(e.target.value) 
                              }
                              const regex2 = new RegExp(/\d{11}/)
                              if(e.target.value.toString().match(regex2)&& e.target.value.toString().length === 11){
                                setNin(e.target.value)
                              }else{
                                setNin('')
                              }
                            }
  const onSubmitNin = () =>{
                              dispatch(updateUserNin(nin))
                            }
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
  })
  return(
    <div
      id="form-body"
      className="form-body">
      <h1 className="form-header">Do you have NIN?</h1>
      <div className="form-section">
      <div className="custom-control custom-radio custom-control-inline">
        <input
          type="radio"
          id="haveNin"
          name="nin"
          value={nin}
          className="custom-control-input"
          onChange ={onSelectYes}
          />
        <label className="custom-control-label" htmlFor="haveNin">Yes</label>
      </div>
      <div className="custom-control custom-radio custom-control-inline">
        <input
          type="radio"
          id="noNin"
          name="nin"
          value=""
          className="custom-control-input"
          onChange ={onSelectNo}
          />
        <label className="custom-control-label" htmlFor="noNin">No</label>
      </div>
      </div>
      <div className="form-section">
      <input
        className="form-control mx-2"
        onChange={onNinChange}
        placeholder="11-digit-number"
        hidden={inputVis}
        value = {inputValue}
      />
      <button
        type="button"
        className="btn btn-success mx-2"
        hidden={btnVis}
        onClick = {onSubmitNin}
        disabled = {!canSubmit}
        >Submit</button>
      </div>
    </div>
  )
}
