import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { updateUserVoteId } from './usersSlice'

export const VoteId = () =>{
  const [voteid, setVoteId] = useState('')
  const [inputVis, setInputVis] = useState('hidden')
  const [inputValue, setInputValue] = useState('')
  const [btnVis, setBtnVis] = useState('hidden')
  const dispatch = useDispatch()
  const onSelectYes = e =>  {setInputVis(''), setBtnVis(''), setVoteId('')}
  const onSelectNo = e => {setInputVis('hidden'), setBtnVis(''), setVoteId('pending')}
  const canSubmit = Boolean(voteid)
  const onVotIdChange = e => {
                                const regex1 = new RegExp(/\W/)
                                if(!e.target.value.toString().match(regex1)||e.target.value.length === 0){
                                  setInputValue(e.target.value) 
                                }
                                const regex2 = new RegExp(/\W{19}/)
                                if(!e.target.value.toString().match(regex2)&& e.target.value.toString().length === 19){
                                  setVoteId(e.target.value)
                                }else{
                                  setVoteId('')
                                }
                              }
  
  const onSubmitVotId = () =>{
                                dispatch(updateUserVoteId(voteid))
                              }
  useEffect(()=>{
    document.getElementById('form-body').classList.remove('form-body');
    document.getElementById('form-body').classList.add('transformed-form-body');
  })
  return(
    <div
    id="form-body"
    className="form-body">
      <h1 className="form-header">Do you have voter's Id?</h1>
      <div className="form-section">
        <div className="custom-control custom-radio custom-control-inline">
          <input
            type="radio"
            id="haveVotid"
            name="voteid"
            value={voteid}
            className="custom-control-input"
            onChange ={onSelectYes}
            />
          <label className="custom-control-label" htmlFor="haveVotid">Yes</label>
        </div>
        <div className="custom-control custom-radio custom-control-inline">
          <input
            type="radio"
            id="noVotid"
            name="voteid"
            value=""
            className="custom-control-input"
            onChange ={onSelectNo}
            />
          <label className="custom-control-label" htmlFor="noVotid">No</label>
        </div>
      </div>
      <div className="form-section">
        <input
          className="form-control mx-2"
          onChange={onVotIdChange}
          value={inputValue}
          placeholder="19-Alphanumerical-Id"
          hidden={inputVis}
        />
        <button
          type="button"
          className="btn btn-success mx-2"
          hidden={btnVis}
          onClick = {onSubmitVotId}
          disabled = {!canSubmit}
          >
          Submit
          </button>
      </div>
    </div>
  )
}
