import React, {useMemo} from 'react'
import { useSelector} from 'react-redux'
import { createSelector } from '@reduxjs/toolkit'
import { loggedUser, votedCand } from '../users/usersSlice'
import { useGetPollsQuery } from '../api/apiSlice'
import { NullCandidate } from './nullcandidate'
import { Link } from 'react-router-dom'
import {VoteButton} from './voteButton'
import {WithdrawVoteButton} from './withdrawVoteButton'




let officePoll = (props) => {
  const  user = useSelector(loggedUser)
  const officeId = props.office.officeId
  const selectOfficePoll = useMemo(()=>{
    const emptyPolls = [] 
    return createSelector(
      res => res.data,
      (res, officeId) => officeId,
      (data, officeId) => data?.filter(poll => poll.officeId === officeId) ?? emptyPolls
    )
  }, []) 
  const {orderedCandidates} = useGetPollsQuery(`?path=${user.uid}`, {
      selectFromResult: result => ({
        ...result,
        orderedCandidates: selectOfficePoll(result, officeId)
      })
  })
 // const orderedCandidates = sortedpolls.filter(poll => poll.officeId === props.office.officeId)
  const topThreeCandidates = orderedCandidates.filter((a, b)=> b < 3)
  const albPartyCandidates = topThreeCandidates.slice().sort((a,b)=>{
                                                  if (a.partyAbbv < b.partyAbbv){
                                                    return -1
                                                  }
                                                  if (a.partyAbbv > b.partyAbbv){
                                                    return 1
                                                  }
                                                  return 0
                                                  }) 
  //const pollStatus = useSelector(status)
  //const error = useSelector(errorStatus)
  
  const votedCandidate = useSelector(state => votedCand(state, props.office.office))


  const officeCandidates = albPartyCandidates.map(candidate => (
    <article className="poll-excerpt" key={candidate._id}>
      <h2 className="poll-candidate-name">{candidate.firstName} {candidate.lastName}</h2>
      <h5>{candidate.partyAbbv}</h5>
      <h6>{candidate.partyName}</h6>
      <h3 className="poll-figure">{candidate.poll}</h3>
      { user.hasOwnProperty('votedOffices') 
        ?
        !votedCandidate ?  <VoteButton candidate={candidate}/>:<WithdrawVoteButton candidate={candidate}/>
        :
        null
      }
    </article>
  ))
  let content
  if (officeCandidates.length === 0){
    content = <NullCandidate/>
  }else if (user.hasOwnProperty('votedOffices')) {
    content = officeCandidates
  }else{
    content = <article className="poll-excerpt" style={{color:'#a84432'}}><div>{error}</div></article>
  }


  return (
    <section className="poll-list">
      <div className="leadtitle">
        <Link to={`/polls/${props.office.officeId}/${props.office.office}`}><h1>{props.office.office} Poll</h1></Link>
      </div>
      <div className="presidential-block">{content}</div>
    </section>
  )
}

export const OfficePoll = React.memo(officePoll)
